<template>
  <div class="c">
    <div class="demo-text">
      <h3>codeMirror</h3>
      <p>场景概述：应用于接口微服务项目当中，该项目是通过「数据查询 mybatis」「消息共享 Redis发布订阅」「服务监控 prometheus」向用户提供接口服务，其中sql语言录入模块是一重要模块，向使用者通过了动态表单配置和sql语句两种方案，单纯使用input框的场景不仅仅外观不够美观，体验也很差，故而选择codeMirror。
      </p>
      <p>组件简介：vue-codemirror可以通过引入不同的js包来配置语言和样式，同时为了体验更好可以引入格式化工具sqlFormatter对代码格式化</p>
      <p>tips：F2格式化代码、Ctrl补全代码
      </p>
    </div>
    <codemirror v-model="item"
      :options="cmOption"
      class="code-mirror"
      ref="codeMirror"
      @ready="onCmReady3"
      @input="onCmCodeChange"
      @keyHandled="keyHandled"></codemirror>
  </div>
  <!-- @focus="onCmFocus" -->
</template>

<script>
// import codemirror from 'vue-codemirror'
import 'codemirror/lib/codemirror.css'
// 我这里引入的是JS语言文件
import 'codemirror/mode/javascript/javascript.js'
// 格式化
import sqlFormatter from 'sql-formatter'
// import  from 'sql-formatter'

import 'codemirror/addon/hint/sql-hint.js'
import 'codemirror/mode/sql/sql.js'
import './setting.js'
// console.log(sqlFormatter, format)
export default {

  props: ['context'],
  watch: {
    context (newVal) {
      this.item = newVal
    }
  },
  data () {
    return {
      item: this.context,
      cmOption: {
        tabSize: 2, // tab
        mode: 'sql', // 模式, 可查看 codemirror/mode 中的所有模式
        lineNumbers: true, // 显示行号
        styleActiveLine: true, // 高亮选中行
        styleSelectedText: true,
        matchBrackets: true, // 括号匹配
        line: true,
        foldGutter: true, // 块槽
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }, // 可以启用该选项来突出显示当前选中的内容的所有实例
        // hint.js options
        hintOptions: {
          // 当匹配只有一项的时候是否自动补全
          completeSingle: true
        },
        // 快捷键 可提供三种模式 sublime、emacs、vim
        keyMap: 'sublime',
        showCursorWhenSelecting: true,
        theme: 'monokai', // 主题
        extraKeys: { Ctrl: 'autocomplete', F2: 'format' }// 可以用于为编辑器指定额外的键绑定，以及keyMap定义的键绑定
      }
    }
  },
  methods: {
    /* 代码格式化 */
    format () {
      /* 获取文本编辑器内容 */
      console.log(sqlFormatter)
      let sqlContent = ''
      sqlContent = this.$refs.codeMirror.codemirror.getValue()
      /* 将sql内容进行格式后放入编辑器中 */
      this.$refs.codeMirror.codemirror.setValue(sqlFormatter.format(sqlContent))
    },
    onCmReady3 () {
      this.$refs.codeMirror.codemirror.setSize('100%', '600')
    },
    keyHandled (cm, name, e) {
      if (name === 'Cmd-Enter') {
        this.format()
      }
    },
    onCmCodeChange (newCode) {
      this.$emit('outSQL', newCode)
    }
  }
}
</script>
<style lang="scss" scoped>
.c {
  display: flex;
  justify-content: space-between;
}
.CodeMirror-scroll {
  overflow: auto !important;
  margin-bottom: 0;
  margin-right: 0;
  padding-bottom: 0;
  height: 100%;
  outline: none;
  position: relative;
}
.code-mirror {
  /* height: 300px; */
  width: 49%;
  font-size: 13px;
  line-height: 150%;
  text-align: left;
}
</style>
